import jsCookie from 'js-cookie';
import { useEffect } from 'react';

function useAuth() {
  useEffect(() => {
    const user = jsCookie.get('JSESSIONID');

    console.log(user);

    if (!user) {
      document.location.href = '/customLogin';
    } else {
      localStorage.setItem('jsessionId', user);
    }
  }, []);
}

export default useAuth;